@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
  height: 100%;
  padding: 0 2rem;
  background-color: #202020;
  color: #fff;
  font-size: 1.05rem;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  padding: 3rem 0 2rem 0;
}

.description {
  font-size: 1.25rem;
  word-spacing: 4px;
}

.myHr {
  height: 1px;
  width: 110%;
  margin: 1.25rem 0 1rem 0;
  outline: none;
  border: none;
  background-color: #ffffff;
}

.button {
  height: 2rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 0.75rem;
  padding: 0 0.5rem;
  outline: none;
  border: 2px solid #ffffff;
  border-radius: 6px;
  font-size: 1rem;
  font-weight: 600;
  color: #ffffff;
  background-color: #1a1a1a;
  transition: 0.5s;
}

.button:active {
  background-color: #e6e6e6;
  transition: 0.1s;
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: start;
  font-size: 0.9rem;
}

@media only screen and (min-width: 600px) {
  .container {
    padding: 0 23%;
    font-size: 1.15rem;
  }

  .description {
    font-size: 1.35rem;
  }

  .myHr {
    width: 100%;
    margin: 1.75rem 0 1.5rem 0;
  }

  .button:hover {
    border-color: #000000;
    border-width: 0 0 0 2px;
    color: #000000;
    background-color: #ffffff;
    transition: 0.3s;
  }

  .button:active {
    background-color: #e6e6e6;
    transition: 0.1s;
  }

  .footer {
    text-align: end;
  }
}

@media only screen and (min-width: 1600px) {
  .title {
    font-size: 3.5rem;
    padding: 3.5rem 0 2rem 0;
  }

  .container {
    font-size: 1.25rem;
  }

  .description {
    font-size: 1.45rem;
  }

  .myHr {
    width: 100%;
    margin: 2rem 0 1.75rem 0;
  }
}

.managerContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  width: 85%;
}

.lotteryContainer,
.inputContainer,
.inputSubContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.lotteryContainer {
  width: 100%;
}

.inputContainer {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 1rem;
  border: 2px solid #ffffff;
  border-radius: 10px;
  padding: 0.5rem 0.75rem;
}

.inputSubContainer {
  -webkit-flex-direction: row;
          flex-direction: row;
  margin: 0.5rem 0;
}

.inputSubContainer .button {
  border-radius: 0 6px 6px 0;
}

.input {
  height: 2rem;
  border: solid #ffffff;
  border-width: 0 0 0 2px;
  border-radius: 6px 0 0 6px;
  outline: none;
  padding-left: 5px;
  font-size: inherit;
}

.text {
  margin-top: 0.5rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.inputContainer .text {
  margin: 0;
}

.lotteryContainer .myHr {
  width: 110%;
}

.inputSubContainer .button{
  margin: 0;
}

@media only screen and (min-width: 600px) {
  .inputContainer {
    padding: 0.75rem 1rem;
  }

  .inputSubContainer {
    margin: 0.75rem 0;
  }
}

